//overwrite original
#membershipDiv{
    padding: 40px 20px !important;
}

html{
    overflow-x: initial;
}

#membershipModal,#membershipModalVIP {
    .membership{
        padding:0;
    }
    .list{
        list-style:none;
    }
    ul li{
        &:before{
            display: none;
        }
    }
    .membership__features ul{
        padding-left: 0;
    }
    .section__title--borderLeft {
        border-left: 5px solid #11a847;
        padding-left: 10px;
        text-align: left;
        font-size: 25px;
        line-height: 22px;
        font-weight: bold;
        margin-bottom: 20px;
    }
    .section__container {
        background-color: #f7faf8;
        padding: 40px 10px;
        border-bottom: 2px solid #11a847;
        border-top: 1px solid #e5e5e5;
        border-left: 1px solid #e5e5e5;
        border-right: 1px solid #e5e5e5;
    }
}